.hhhhhh {
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 1000;
    width: 100%;
    background-color: rgb(255, 133, 42);
}
.header-bd {
    width: 100%;
    padding: 6px 9px 6px 42px;
    background-image: url(../assets/taobao.PNG);
    background-repeat: no-repeat;
    background-size: 30px 30px;
    background-position: 4px center;
}
.header-bd .placeholder {
    width: 85%;
    display: block;
    font-size: 0px;
    background-color: rgb(255, 78, 34);
    text-align: center;
    border-radius: 4px;
}
.header-bd .placeholder .text {
    height: 25px;
    line-height: 25px;
    font-size: 13px;
    display: inline-block;
    color: rgb(240, 224, 220);
}
.lunbotu{
    margin-top: 30px;
    margin-right: 2px;
}
.recommend-info {
    padding-left: 10px;
    padding-right: 9px;
    height: 94px;
}
.recommend-item {
    height: 260px;
    position: relative;
    width: 170px;
    box-sizing: border-box;
    display: -webkit-flex;
    flex-direction: column;
    background: #fff;
    margin-top: 9px;
    border-radius: 6px;
    overflow: hidden;
}
.recommend-body{
    position: relative;
    display: flex;
    flex-wrap: wrap;
    -webkit-box-pack: justify;
    justify-content: space-between;
}
.recommend {
    display: -webkit-flex;
    display: flex;
    position: relative;
    box-sizing: border-box;
    flex-direction: row;
    place-content: flex-start space-between;
    flex-shrink: 0;
    overflow: hidden;
    flex-wrap: wrap;
    padding: 0 7.5px;
    background: #f2f2f2;
    padding-bottom: 12px;
    
}
.recommend-img {
    width: 171px;
    height: 171px;
}
.recommend-title-p {
    font-size: 14px;
    color: #333333;
}
.recommend-h {
    color: #ff5500;
}
.recommend-price {
    font-size: 16px;
    line-height: 16px;
    color: #ff5500;
}
.recommend-payed {
    font-size: 11px;
    color: #999999;
    margin-top: 3px;
    margin-left: 10px;
}
.recommend-title {
    margin-top: 13px;
    height: 37px;
    position: relative;
    overflow: hidden;
    line-height: 18.5px;
    text-overflow: ellipsis;
    min-height: 0;
}
.recommend-price-box {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    flex-direction: row;
    margin-top: 11px;
    height: 20px;
    line-height: 20px;
}
.recommend-img{
    -webkit-box-align: center;
    width: 171px;
    height: 171px;
    align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
}
.footer{
    height: 170px;
    background-color: rgb(78, 82, 94);
}
.denglu {
    width: 100%;
    text-align: center;
    justify-content: space-between;
}
.denglu a {
    margin-top: 15px;
    margin-left: 10px;
    display: inline-block;
    height: 35px;
    line-height: 35px;
    color: rgb(195, 198, 202);
    border: 1px solid rgb(66, 69, 77);
    background-color: rgb(85, 89, 101);
    padding: 0px 17px;
    border-radius: 6px;
    font-size: 14px;
}
.fuwu {
    font-size: 12px;
    color: rgb(195, 198, 202);
    text-align: center;
    width: 100%;
    margin-top: 20px;
    height: 20px;
    line-height: 20px;
}