.bottom-bar {
    position: fixed;
    bottom: 0;
    width: 100%;
    display: flex;
    align-items: center;
    background-color: #fff;
    padding-left: 0.1rem;
    height: 50px;
}
.bottom-bar > div:first-child {
    padding-left: 0;
    margin-left: 0;
}
.bottom-bar > div {
    display: flex;
    flex-direction: column;
    color: #999;
    padding: 0 0.4rem;
}
.bottom-bar .bottom-bar-btn {
    width: 30% !important;
    bottom: 0.12rem;
    text-align: center;
    padding: 0;
    line-height: 260%;
    color: #fff;
}
.bottom-bar .bottom-bar-btn.btn-pos-right {
    border-radius: 0 5rem 5rem 0;
    background-image: -webkit-linear-gradient(left, #FF7A00, #FE560A);
    background-image: linear-gradient(to right, #FF7A00, #FE560A);
    height: 70%;
}
.bottom-bar .bottom-bar-btn.btn-pos-left {
    border-radius: 5rem 0 0 5rem;
    background-image: -webkit-linear-gradient(left, #FFC500, #FF9402);
    background-image: linear-gradient(to right, #FFC500, #FF9402);
    height: 70%;
    margin-left: 7%;
}

.price-symbol{
    color: #ff5000;
    font-size: 25px;
    padding-left: 2%;
}
.price{
    color: #ff5000;
    font-size: 35px;
    padding-left: 2%;
}
.information {
    display: -webkit-box;
    line-height: 20px;
    overflow: hidden;
    color: rgb(51, 51, 51);
    font-weight: bold;
    font-size: 15px;
    padding-left: 2%;
}
.num{
    white-space: nowrap;
    line-height: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    color: rgb(153, 153, 153);
    visibility: visible;
    font-size: 13px;
    padding-left: 2%;
}